@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 400;
  font-display: swap;
  src: url("../fonts/opensans.woff2") format("woff2"), url("../fonts/opensans.woff") format("woff");
}
@font-face {
  font-family: "OpenSans";
  font-style: normal;
  font-weight: 700;
  font-display: swap;
  src: url("../fonts/opensansbold.woff2") format("woff2"), url("../fonts/opensansbold.woff") format("woff");
}
.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  border: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
}

img {
  max-width: 100%;
  height: auto;
}

* {
  box-sizing: border-box;
}

.page {
  height: 100%;
  font-family: "OpenSans", "Arial", sans-serif;
  font-size: 17px;
  line-height: 24px;
  font-weight: 700;
}

.page__body {
  display: flex;
  flex-direction: column;
  min-height: 100%;
  color: #000000;
  margin: 0;
}

.page-title {
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 27px;
  line-height: 30px;
}

.advertisement {
  display: flex;
  width: 100%;
}

.advertisement__slogan {
  justify-content: center;
  align-items: center;
  margin: 70px 30px auto 30px;
}

.advertisement__title {
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 700;
  text-align: center;
}

.advertisement__container {
  display: flex;
  flex-wrap: wrap;
}

.advertisement__name {
  font-family: "OpenSans", "Arial", sans-serif;
  font-weight: 700;
  font-size: 19px;
  line-height: 24px;
  width: 50%;
  color: #ffffff;
  margin: 0;
}

.advertisement__features {
  display: flex;
  width: 100%;
  height: 103px;
  position: relative;
}

.advertisement__features--light {
  background-color: #63d1bb;
}

.advertisement__features--light::before {
  position: absolute;
  top: 23px;
  right: 36px;
  content: url("../img/images-svg/interior-feature.svg");
  z-index: 1;
}

.advertisement__features--dark {
  background-color: #56c3b2;
}

.advertisement__features--dark::before {
  position: absolute;
  top: 23px;
  right: 36px;
  content: url("../img/images-svg/toy-feature.svg");
  z-index: 1;
}

.advertisement__link {
  margin-left: 30px;
  margin-top: 24px;
  text-decoration: none;
}

@media (max-width: 771px) {
  .advertisement__wrapper {
    background-image: url("../img/mobile/hero-mobile@1x.png");
    background-repeat: no-repeat;
  }
}
@media (max-width: 771px) and (min-resolution: 2dppx) {
  .advertisement__wrapper {
    background-image: url("../img/mobile/hero-mobile@2x.png");
  }
}
@media (max-width: 771px) {
  .advertisement__slogan {
    justify-content: center;
    align-items: center;
    margin: 73px 30px auto 30px;
  }
}
@media (max-width: 771px) {
  .advertisement__container {
    flex-direction: column;
  }
}
@media (max-width: 771px) {
  .advertisement__title {
    font-size: 27px;
    line-height: 30px;
    margin-bottom: 120px;
    margin-top: 73px;
  }
}
@media (min-width: 772px) {
  .advertisement__wrapper {
    background-image: url("../img/tablet/hero-tablet@1x.png");
  }
}
@media (min-width: 772px) and (min-resolution: 2dppx) {
  .advertisement__wrapper {
    background-image: url("../img/tablet/hero-tablet@2x.png");
  }
}
@media (min-width: 772px) {
  .advertisement__container {
    display: flex;
    flex-wrap: wrap;
    flex-direction: row;
  }
}
@media (min-width: 1150px) {
  .advertisement__wrapper {
    background-image: url("../img/desktop/hero-desktop@1x.png");
  }
}
@media (min-width: 1150px) and (min-resolution: 2dppx) {
  .advertisement__wrapper {
    background-image: url("../img/desktop/hero-desktop@2x.png");
