.main-header {
  display: flex;
  flex-direction: row;
  background-color: #ffffff;
  color: #000000;
  justify-content: center;
}

.main-header__logo {
  border: none;
}
.main-header__logo:hover {
  opacity: 0.6;
}
.main-header__logo:active {
  opacity: 0.3;
}

@media (max-width: 771px) {
  .main-header {
    position: relative;
    flex-direction: column;
  }

  .main-header__wrapper {
    display: flex;
    flex-direction: row;
  }

  .main-header__logo {
    height: 35px;
    width: 86px;
    background-image: url("../img/mobile/logo-mobile.svg");
    background-repeat: no-repeat;
    margin-left: 116px;
    margin-right: 67px;
    margin-top: 22px;
    margin-bottom: 22px;
  }

  .main-header__toggle {
    position: relative;
    background: url("../img/images-svg/menu-opening.svg") center no-repeat;
    background-color: transparent;
    top: 32px;
    right: 0;
    width: 20px;
    height: 14px;
    cursor: pointer;
    justify-self: end;
    border: none;
  }

  .main-header__toggle--nojs {
    display: none;
  }

  .main-header__toggle--nojs ~ .main-header__toggle {
    display: none;
  }

  .main-header__toggle.active {
    background: url("../img/images-svg/menu-close.svg") center no-repeat;
  }
}
@media (min-width: 772px) {
  .main-header {
    height: 89px;
  }

  .main-header__wrapper {
    display: flex;
  }

  .main-header__toggle {
    display: none;
  }

  .main-header__logo {
    height: 46px;
    width: 113px;
    background: url("../img/tablet/mishka-logo-tablet.svg") center no-repeat;
  }
}
.main-nav {
  display: flex;
}

.main-nav__wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

@media (max-width: 771px) {
  .main-nav {
    position: absolute;
    top: 49px;
    left: 0;
    width: 100%;
    z-index: 1;
    min-height: 316px;
    background-color: #ffffff;
  }

  .main-nav--nojs {
    display: block;
    position: static;
  }

  .main-nav--opened {
    min-height: 200px;
  }

  .main-nav--closed {
    display: none;
  }

  .main-nav--closed .main-header__toggle--nojs {
    display: none;
  }

  .main-nav--closed .site-list {
    display: none;
  }

  .main-nav--opened .main-nav__wrapper {
    position: absolute;
    top: 0;
    right: 0;
    left: 70px;
    z-index: 10;
    min-height: 80vh;
  }

  .main-nav--opened .main-nav__wrapper::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    z-index: 2;
    width: 7px;
    height: 100%;
    box-shadow: inset 4px 0 7px -2px fade(#000000, 40%);
  }

  .main-nav--nojs .main-nav__wrapper {
    position: static;
    min-height: 0;
  }

  .main-nav--nojs .site-list__item {
    display: flex;
  }

  .main-nav--nojs .site-list__item--active a {
    padding-right: 21px;
    padding-left: 21px;
    text-align: center;
  }

  .site-list__link {
    background-color: #ffffff;
    color: #000000;
    margin: 0 auto;
    text-transform: none;
    text-decoration: none;
    justify-content: center;
    margin-top: 28px;
    margin-bottom: 26px;
  }

  .main-nav__add {
    display: none;
  }
}
